html,
body {
  width: 1200px;
  margin: 0 auto;
  color: #444444;
}

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

*,
*:before,
*:after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit;
}

.page-header {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  height: 60px;
  padding: 15px;
  background-color: #fdf8e5;
}

.page-header__title {
  margin-top: 0;
  margin-bottom: 0;
  padding-left: 35px;
  font-size: 24px;
  line-height: 30px;
  font-weight: 300;
  background-image: url("icons/sun.svg");
  background-size: 30px;
  background-repeat: no-repeat;
}

.page-header__search {
  width: 300px;
  height: 30px;
  font-size: 14px;
  line-height: 24px;
  padding: 3px 5px;
}

.daily-forecast {
  padding: 15px 5px 10px;
}

.daily-forecast__date {
  font-size: 14px;
}

.daily-forecast__city-name {
  font-size: 18px;
  text-align: center;
}

.daily-forecast__temperature {
  font-weight: 700;
  font-size: 28px;
  text-align: center;
}

.daily-forecast__text {
  text-align: center;
  margin-bottom: 10px;
}

.daily-forecast__icon {
  width: 30px;
  height: 30px;
}

.daily-forecast__extra-info {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  height: 24px;
  padding: 3px 5px;
  line-height: 18px;
  font-size: 16px;
  text-align: center;
  background-color: #fdf8e5;
}

.daily-forecast__info-item:not(:last-child) {
  margin-right: 15px;
}

.tabs {
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 10px;
  padding-left: 0;
  list-style: none;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: start;
      justify-content: flex-start;
}

.tabs__item {
  padding: 5px;
  border: 1px solid #cccccc;
}

.tabs__item:hover {
  cursor: pointer;
}

.tabs__item:not(:first-child) {
  margin-left: -1px;
}

.tabs__item--active {
  background-color: #fdf8e5;
  border-bottom-width: 0;
}

.long-term-forecast__chart {
  display: -ms-flexbox;
  display: flex;
  height: 230px;
  background-color: #fdf8e5;
  padding: 10px;
}

.long-term-forecast__chart-item {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: end;
      justify-content: flex-end;
  text-align: center;
  width: 85px;
}

.long-term-forecast__chart-item:not(:last-child) {
  margin-right: 10px;
}

.long-term-forecast__chart-column {
  min-height: 25px;
  width: 100%;
  padding-top: 5px;
  color: #ffffff;
  -webkit-transition: all 0.3s ease;
  -o-transition: all 0.3s ease;
  transition: all 0.3s ease;
  background-size: 20px;
  background-repeat: no-repeat;
  background-position: 5px 3px;
}

.long-term-forecast__chart-column--type-wind {
  background-color: #0074d9;
  background-image: url("icons/breeze.svg");
}

.long-term-forecast__chart-column--type-temp {
  background-color: #ff851b;
  background-image: url("icons/celsius.svg");
}

.long-term-forecast__chart-column--type-presure {
  background-color: #3d9970;
  background-image: url("icons/gauge.svg");
  padding-left: 20px;
}

.long-term-forecast__chart-column--type-humidity {
  background-color: #39cccc;
  background-image: url("icons/humidity.svg");
  padding-left: 10px;
}

.loader {
  text-align: center;
}

.loader img {
  -webkit-animation: Loader-spin infinite 5s linear;
          animation: Loader-spin infinite 5s linear;
}

@-webkit-keyframes Loader-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes Loader-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.error-msg {
  position: absolute;
  top: 0;
  left: 50%;
  -webkit-transform: translateX(-50%);
      -ms-transform: translateX(-50%);
          transform: translateX(-50%);
  height: 34px;
  padding: 5px 10px;
  color: #fffdfd;
  font-size: 16px;
  line-height: 24px;
  background-color: #f65656;
}



